.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    
  }
  img{
    width: 60%;
    height: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto; 
    margin-bottom: auto; 

  }

  .card:hover { 
    -webkit-box-shadow: 0 5px 10px rgb(63, 63, 63), 0 5px 10px  rgb(63, 63, 63); 
            box-shadow: 0 5px 10px rgb(63, 63, 63), 0 5px 10px  rgb(63, 63, 63);
}


